import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useRecoilState } from "recoil";
import { jwtTokenState, isAuthenticatedState } from "../../recoil/atoms/auth";

import styled from "@emotion/styled";
import bcrypt from "bcryptjs";

const LoginWrap = styled.form`
  margin-bottom: 10px;
  padding-bottom: 10px;
  width: 350px;
  height: 400px;
  border: 1px solid #ccc;
`;
const LoginLogoWrap = styled.div`
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  flex-direction: column;
  align-items: center;
  margin: 0 0 10px;
  padding: 10px 0;
  vertical-align: baseline;
`;
const LoginLogo = styled.div`
  display: inline-block;
  margin-top: 36px;
  margin-bottom: 12px;
  width: 175px;
  height: 51px;
  background-image: url(https://static.cdninstagram.com/rsrc.php/v3/yK/r/ATdtiLb2BQ9.png);
  background-position: 0px 0px;
  background-size: 176px 181px;
  background-repeat: no-repeat;
`;
const LoginInputWrap = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 40px 6px;
  border-radius: 3px;
  background-color: #fafafa;
  border: 1px solid rgb(219, 219, 219);
`;
const LoginInput = styled.input`
  overflow: hidden;
  border: 0;
  flex: 1 0 auto;
  margin: 0;
  outline: none;
  padding: 9px 0 2px 8px;
  height: 38px;
  font-size: 12px;
  background: rgb(var(--ig-secondary-background));
  text-overflow: ellipsis;
`;
const LoginInputSpan = styled.span`
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 8px;
  line-height: 36px;
  color: #ccc;
  font-size: 12px;
  transition: 0.2s linear all;
  &.notEmpty {
    /* 클래스가 존재할 때의 스타일 설정 */
    top: -8px;
    font-size: 10px;
    /* 원하는 스타일 추가 */
  }
`;
const LoginButtonWrap = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 8px 40px;
`;
const LoginButton = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7px 16px;
  width: 100%;
  font-size: 12px;
  color: #fff;
  background-color: rgb(0, 149, 246);
  border-radius: 8px;
  opacity: 0.7;
  &:disabled {
    background-color: #ccc;
  }
`;

function LoginBox() {
  const [jwtToken, setJwtToken] = useRecoilState(jwtTokenState);
  const [loggedIn, setLoggedIn] = useRecoilState(isAuthenticatedState);

  type FormData = {
    idOrUsername: string;
    password: string;
  };

  const [formData, setFormData] = useState<FormData>({
    idOrUsername: "",
    password: "",
  });

  const navigate = useNavigate();
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };
  const isFormValid = () => {
    return formData.idOrUsername && formData.password;
  };
  const isInputNotEmpty = (name: keyof FormData) => {
    return formData[name].length > 0;
  };
  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault(); // 기본 제출 동작 중지

    const storedUsers = Object.keys(localStorage);

    const foundUser = storedUsers.find((storedUser) => {
      const user = JSON.parse(localStorage.getItem(storedUser) || "");

      // 아래 코드에서 formData.id 또는 formData.username 중 하나만 비교하도록 변경
      return (
        user &&
        (user.id === formData.idOrUsername ||
          user.username === formData.idOrUsername)
      );
    });

    if (foundUser) {
      const user = JSON.parse(localStorage.getItem(foundUser) || "");

      const isPasswordMatch = await bcrypt.compare(
        formData.password,
        user.password
      );
      if (isPasswordMatch) {
        const token = "your-jwt-token";
        setJwtToken(token);
        setLoggedIn(true);
        localStorage.setItem("jwtToken", "your-jwt-token-here");

        navigate("/contents");
      } else {
        alert("로그인 실패. 비밀번호가 일치하지 않습니다.");
      }
    } else {
      alert("로그인 실패. 사용자 정보가 일치하지 않습니다.");
    }
  };

  return (
    <LoginWrap onSubmit={handleLogin}>
      <LoginLogoWrap>
        <LoginLogo></LoginLogo>
      </LoginLogoWrap>
      <LoginInputWrap>
        <LoginInput
          type="text"
          name="idOrUsername" // 하나의 인풋으로 id 또는 username을 입력받도록 수정
          value={formData.idOrUsername}
          onChange={handleInputChange}
          autoComplete="off"
        />
        <LoginInputSpan
          className={isInputNotEmpty("idOrUsername") ? "notEmpty" : ""}
        >
          전화번호, 사용자 이름 또는 이메일
        </LoginInputSpan>
      </LoginInputWrap>
      <LoginInputWrap>
        <LoginInput
          type="password"
          name="password"
          value={formData.password}
          onChange={handleInputChange}
          autoComplete="off"
        />
        <LoginInputSpan
          className={isInputNotEmpty("password") ? "notEmpty" : ""}
        >
          비밀번호
        </LoginInputSpan>
      </LoginInputWrap>
      <LoginButtonWrap>
        <LoginButton type="submit" disabled={!isFormValid()}>
          로그인
        </LoginButton>
      </LoginButtonWrap>
    </LoginWrap>
  );
}

export default LoginBox;
