import React, { useState, useEffect, useCallback, useRef } from "react";
import styled from "@emotion/styled";
import PostOpen from "./PostOpen";
const PostWrap = styled.div`
  margin: 0 auto;
  padding: 24px;
  padding-bottom: 120px;
`;

const PostList = styled.ul`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  flex-wrap: wrap;
  gap: 4px;
`;

const PostItem = styled.li`
  width: 100%;
  aspect-ratio: 1/1;
  background-color: #eee;
  cursor: pointer;
`;

function Post() {
  const data = Array.from({ length: 66 }, (_, index) => index + 1);
  const startIndex = 0;
  const itemsPerPage = 9;
  const [endIndex, setEndIndex] = useState(startIndex + itemsPerPage - 1);
  const [items, setItems] = useState(
    data.filter((_, index) => index >= startIndex && index <= endIndex)
  );
  const [fetching, setFetching] = useState(false);
  const [isPostOpen, setIsPostOpen] = useState(false);
  function handleClickIsPostOpen() {
    setIsPostOpen(true);
  }
  const handleClose = () => {
    setIsPostOpen(false);
  };
  const fetchMoreInstaFeeds = useCallback(() => {
    if (data.length > endIndex) {
      setFetching(true);
      const newEndIndex = endIndex + 9;
      setEndIndex(newEndIndex);

      // 여기서 setTimeout을 사용하여 딜레이를 줍니다.
      setTimeout(() => {
        setItems(
          data.filter((_, index) => index >= startIndex && index <= newEndIndex)
        );
        setFetching(false);
      }, 0); // 1초 딜레이를 준 예시, 필요에 따라 조절할 수 있습니다.
    } else {
      setEndIndex(data.length);
      setItems(
        data.filter((_, index) => index >= startIndex && index <= endIndex)
      );
      setFetching(true);
    }
  }, [data, endIndex, startIndex]);

  const handleScroll = useCallback(() => {
    const scrollHeight = document.documentElement.scrollHeight;
    const scrollTop = document.documentElement.scrollTop;
    const clientHeight = document.documentElement.clientHeight;

    if (scrollTop + clientHeight >= scrollHeight && fetching === false) {
      // 페이지 끝에 도달하면 추가 데이터를 받아옵니다.
      if (!initialScrollTriggered.current) {
        // 처음 스크롤 시에는 딜레이 없이 실행
        if (scrollTop + clientHeight === scrollHeight) {
          fetchMoreInstaFeeds();
          initialScrollTriggered.current = true;
        }
      } else {
        // 그 이후에는 딜레이를 주고 실행
        setTimeout(() => {
          fetchMoreInstaFeeds();
        }, 100); // 1초 딜레이를 준 예시, 필요에 따라 조절할 수 있습니다.
      }
    }
  }, [fetchMoreInstaFeeds, fetching]);

  useEffect(() => {
    // 스크롤 이벤트 리스너를 추가합니다.
    window.addEventListener("scroll", handleScroll);

    return () => {
      // 컴포넌트가 언마운트될 때 이벤트 리스너를 제거합니다.
      window.removeEventListener("scroll", handleScroll);
    };
  }, [handleScroll]);

  const initialScrollTriggered = useRef(false);

  useEffect(() => {
    // 초기 스크롤 이벤트를 처리하는 리스너를 연결합니다.
    const handleInitialScroll = () => {
      if (!initialScrollTriggered.current) {
        // 처음 스크롤 시에는 딜레이 없이 실행
        if (
          window.scrollY + window.innerHeight ===
          document.documentElement.scrollHeight
        ) {
          fetchMoreInstaFeeds();
          initialScrollTriggered.current = true;
        }
      }
    };
    window.addEventListener("scroll", handleInitialScroll);

    return () => {
      // 컴포넌트가 언마운트될 때 이벤트 리스너를 제거합니다.
      window.removeEventListener("scroll", handleInitialScroll);
    };
  }, [fetchMoreInstaFeeds]);

  return (
    <>
      {isPostOpen ? <PostOpen handleClose={handleClose} /> : ""}
      <PostWrap className="post-wrap">
        <PostList className="post">
          {items.map((item, index) => (
            <PostItem key={index} onClick={handleClickIsPostOpen}>
              {item}
            </PostItem>
          ))}
        </PostList>
      </PostWrap>
    </>
  );
}

export default Post;
