import { useEffect, useRef } from "react";
import styled from "@emotion/styled";

const StoryWrap = styled.div`
  margin-bottom: 44px;
  height: 130px;
`;
const StoryUl = styled.ul`
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  padding: 0 24px;
  height: 100%;

  /* Webkit 스크롤바 스타일 (Chrome, Safari, Edge) */
  &::-webkit-scrollbar {
    display: none; /* 스크롤바 숨기기 */
  }

  /* Firefox 스크롤바 스타일 */
  scrollbar-width: none; /* 스크롤바 숨기기 */

  /* Internet Explorer 10+ 스크롤바 스타일 */
  -ms-overflow-style: none; /* 스크롤바 숨기기 */
`;
const StoryLi = styled.li`
  position: relative;
  overflow-x: visible;
  overflow-y: visible;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 125px;
`;
const StroyBox = styled.div`
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 115px;
`;
const StoryImgWrap = styled.div`
  position: relative;
  display: block;
  flex: none;
  align-self: center;
`;

const StoryImgCircle = styled.canvas`
  position: absolute;
  left: -5px;
  top: -5px;
  width: 87px;
  max-width: initial;
  height: 87px;
  aspect-ratio: auto 87 / 87;
`;
const StoryImg = styled.span`
  overflow-x: hidden;
  overflow-y: hidden;
  position: relative;
  display: block;
  width: 77px;
  height: 77px;
  border-radius: 50%;
`;
const StoryTxtBlock = styled.div`
  padding-top: 15px;
  width: 100%;
  text-align: center;
`;
const StoryTxtWrap = styled.span`
  overflow-x: visible;
  overflow-y: visible;
  min-width: 0;
  max-width: 100%;
  white-space: pre-line;
  word-wrap: break-word;
  word-break: break-word;
`;
const StoryTxt = styled.span`
  overflow-x: hidden;
  overflow-y: hidden;
  display: block;
  min-width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: break-word;
  word-break: break-word;
`;
function ProfileStory() {
  const data = Array.from(
    { length: 10 },
    (_, index) => `ItemItemItemItem ${index + 1}`
  );

  const canvasRefs = useRef<(HTMLCanvasElement | null)[]>([]);
  if (canvasRefs.current.length !== data.length) {
    canvasRefs.current = Array(data.length)
      .fill(null)
      .map(() => document.createElement("canvas"));
  }

  useEffect(() => {
    canvasRefs.current.forEach((canvas, index) => {
      const width = 87;
      const height = 87;
      const dpr = window.devicePixelRatio;

      if (canvas) {
        const ctx = canvas.getContext("2d");
        canvas.style.width = `${width}px`;
        canvas.style.height = `${height}px`;
        canvas.width = width * dpr;
        canvas.height = height * dpr;
        const borderWidth = 2;
        const radius = Math.min(width, height) - borderWidth / 2;

        if (ctx) {
          const centerX = canvas.width / 2; // 중심 x 좌표
          const centerY = canvas.height / 2; // 중심 y 좌표
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          const gradient = ctx.createLinearGradient(
            centerX - radius,
            centerY,
            centerX + radius,
            centerY
          );
          // #FF0069 빨
          // #FF7A00 주
          // #FFD600 노
          // #D300C5 마
          // #7638FA 보

          gradient.addColorStop(0, "#FF0069"); // 빨강
          gradient.addColorStop(0.25, "#FF7A00"); // 주황
          gradient.addColorStop(0.5, "#FFD600"); // 노랑
          gradient.addColorStop(0.75, "#D300C5"); // 마젠타
          gradient.addColorStop(1, "#7638FA"); // 보라

          // 원 그리기
          ctx.beginPath();
          ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI);
          ctx.strokeStyle = gradient; // 테두리 색상
          ctx.lineWidth = borderWidth; // 테두리 두께
          ctx.stroke(); // 테두리 그리기
          ctx.closePath();
        }
      }
    });
  }, []);

  return (
    <StoryWrap>
      <StoryUl>
        {data.map((item, index) => (
          <StoryLi key={index}>
            <StroyBox>
              <StoryImgWrap>
                <StoryImgCircle
                  ref={(canvas) => (canvasRefs.current[index] = canvas)}
                  width="87"
                  height="87"
                ></StoryImgCircle>
                <StoryImg>
                  <img src="https://via.placeholder.com/77" alt="샘플이미지" />
                </StoryImg>
              </StoryImgWrap>
              <StoryTxtBlock>
                <StoryTxtWrap>
                  <StoryTxt>{item}</StoryTxt>
                </StoryTxtWrap>
              </StoryTxtBlock>
            </StroyBox>
          </StoryLi>
        ))}
      </StoryUl>
    </StoryWrap>
  );
}

export default ProfileStory;
