import styled from "@emotion/styled";
const PostDimmer = styled.div`
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
`;
const PostButtonClose = styled.button`
  position: absolute;
  z-index: 1;
  top: 10px;
  right: 10px;
`;
const PostButtonCloseIcon = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  & svg {
    color: #fff;
  }
`;
const PostBox = styled.div`
  position: fixed;
  top: 50%;
  bottom: 50%;
  left: 50%;
  right: 50%;
  width: 500px;
  height: 500px;
  transform: translate(-50%, -50%);
  background-color: #fff;
  z-index: 1;
`;

function PostOpen({ handleClose }: { handleClose: () => void }) {
  const handleClickIsPostClose = () => {
    // handleClose 함수를 호출하여 부모 컴포넌트에서 isPostOpen 상태를 변경합니다.
    handleClose();
  };
  const handlePostBoxClick = (
    e: React.MouseEvent<HTMLDivElement, MouseEvent>
  ) => {
    e.stopPropagation(); // 클릭 이벤트 전파 중지
    // PostBox 클릭 시 실행할 코드 작성
  };

  return (
    <PostDimmer onClick={handleClickIsPostClose}>
      <PostButtonClose onClick={handleClickIsPostClose}>
        <PostButtonCloseIcon>
          <svg
            aria-label="닫기"
            color="rgb(255, 255, 255)"
            fill="rgb(255, 255, 255)"
            height="18"
            role="img"
            viewBox="0 0 24 24"
            width="18"
          >
            <title>닫기</title>
            <polyline
              fill="none"
              points="20.643 3.357 12 12 3.353 20.647"
              stroke="currentColor"
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="3"
            ></polyline>
            <line
              fill="none"
              stroke="currentColor"
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="3"
              x1="20.649"
              x2="3.354"
              y1="20.649"
              y2="3.354"
            ></line>
          </svg>
        </PostButtonCloseIcon>
      </PostButtonClose>
      <PostBox onClick={handlePostBoxClick}></PostBox>
    </PostDimmer>
  );
}
export default PostOpen;
